var PorkopolisWithClasses=function PorkopolisWithClasses(_ref){var classes=_ref.classes;return __WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement(__WEBPACK_IMPORTED_MODULE_2__Components_EssayPage__["a" /* default */],{articleTitle:'Porkopolis and the Austin-Magie Farm',authorName:'Rob Gioielli'},__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('p',null,'The Austin-Magie Farm was an important part of the economy of the Miami River Valley in the 19th century, but also connected the community to Cincinnati, the rest of the country, and the globe. The farm did this through the region\u2019s primary agricultural product\u2013pigs.'),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('p',null,'Pigs were an important part of all Euro-American farms during the Colonial Period. Because they could graze on their own in the local forests, fields and swamps for months at a time, they were attractive to subsistence-oriented farmers, who didn\u2019t have to tend them on a daily basis. They could simply collect them in the fall for slaughter. When whites settled the Ohio River Valley in the late 18th century, those farm families brought their pigs with them. But once in the valley, they began to look for more commercial farming opportunities. The region\u2019s primary grain crop was corn, but it was too bulky and heavy to cheaply transport to markets, especially when the primary means of transit was a horse-drawn carriage on a muddy road. So, farmers began to condense their corn, into pigs. At first this was just fattening up semi-feral pigs, which had been running free all summer, for a few weeks in the fall. But by the 1820s some farmers were raising hogs in pens and fenced in fields full time to have them grow as robust and as portly as possible for market. That market was Cincinnati. The emerging Queen City of the West would form an important relationship with the farms and farmers of Southwest Ohio, as they would shape each other over the course of the 19th century.'),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('p',null,'Cincinnati was originally one of numerous cities competing for the trade and traffic of the burgeoning Trans-Appalachian West, but it was able to set itself apart with strong river connections, an accessible public landing and an early branch of the national bank. This meant that the larger slaughterhouses and meatpackers in town could find a ready market for their products, allowing them to pay higher prices per pound for live hogs, attracting an ever-growing stream of swine to the city during packing season, peaking at 500,000 a year in the late 1840s. Cincinnati meatpackers also pioneered manufacturing techniques, what we may call the \u201Cdisassembly line,\u201D that allowed them to kill, slaughter and pack hogs at an astonishing rate, further increasing their economies of scale. '),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('h3',null,'Byproducts'),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('p',null,'Because of this proficiency and prominence in the hog business, Cincinnati would quickly earn the nickname \u201CPorkopolis.\u201D But although pork was the city\u2019s primary product (up to 50 million pounds a year), it was not the only one. As soon as pork-packers set up shop in town, other manufacturers moved in to process the myriad of byproducts that came from a slaughtered animal. In the 21st century we are used to most of our chemicals and many of our manufactured goods coming from metals, or petrochemicals. But the industrial world of the 19th century was profoundly organic, and still directly connected to the animal and plant products grown on American farms. Pig\u2019s blood was used in dyes and hundreds of other chemicals. Pig hair and bristles were used in pillows, brushes and even plaster, and bones were also ground down for various food products and compounds. But the most valuable byproduct was fat.'),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('p',null,'Rendered animal fat has long been the primary ingredient in soap and candles. But Cincinnati manufacturers pioneered new methods that made it even more valuable. Through new rendering technologies that involved injecting steam into animal lard, they could produce candles and lard oil that were clean burning, without the animal fat smell of tallow candles. In a rapidly industrializing America, new forms of illuminants were extremely valuable. During the 1850s, Cincinnati lard oil competed with sperm whale oil as the most popular lighting fuel in the United States.'),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('p',null,'The size and sophistication of Cincinnati\u2019s byproduct market is one of the primary reasons why it continued to grow as a pork-packing center in the decades before the American Civil War. Cincinnati packers could pay a few cents more per pound than in surrounding cities, which added up to an extra $100 or more per year for the average regional farmer, big money in the 19th century. That demand for fat would boomerang back to the Miami Valley, shaping its farms, landscape and especially its pigs. '),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('h3',null,'Engineering Better Pigs'),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('p',null,'Early American pigs could fend for themselves for weeks or months at a time because they were built far differently than the docile, domesticated hogs of today. With long snouts, stout legs, and bristly \u201Crazorbacks\u201D they could fend off predators and run at a fast clip. But their meat was also sinewy and chewy, and their fat and lard often mealy and sour. As soon as Cincinnati began to boom as a packing market, local farmers looked for ways to improve their pigs to make them more marketable, and to increase their profits. The first step was bringing them out of the forest and into pens on the farm, where they could be fattened up on a regular diet of corn and farm scraps. Then it was about breeding to encourage certain characteristics, and discourage others. The earliest custom breeds were brought from England, but eventually, farmers in Southwest Ohio were crossing these breeds together in order to create uniquely American strains. '),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('p',null,'The center of custom breeding in the region, and indeed the country, was a belt of farms that ran through northern Hamilton County, Butler and Warren County. Here more prosperous farmers were constantly experimenting to create different breeds, which they would then market to farms all across Southwestern Ohio and Southeastern Indiana. One of the most successful of these was David Magie, who developed the \u201CMagie Hog\u201D in the 1840s, and would later purchase Aaron Austin\u2019s farm in the 1860s. Magie\u2019s pigs were the origins of the Poland-China breed, the first great American commercial hog breed.'),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('p',null,'It is tempting to think of 19th century animal breeding as a quaint practice, but Magie and other Miami Valley breeders were serious \u201Cimproving farmer,\u201D agriculturalists who were geared towards the market, and were constantly looking for ways to improve yields and profitability. Although a far cry from the test tubes and laboratories of today, their breeding was a form of engineering, looking to isolate and emphasize particular factors in their animals. For example, a farmer\u2019s primary calculus was pounds of corn per pound of hog. They wanted a hog to put on the most weight possible for every pound of corn they fed them, decreasing costs and lowering profits. As they bred hogs who were the best \u201Cfeeders,\u201D what they were actually doing was increasing the length of the hog\u2019s intestine. The longer the digestive track, the more nutrients and calories the hog could squeeze out of each kernel of corn. '),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('p',null,'By the 1850s, Miami Valley breeders like Magie were regularly producing hogs that weighed as much as 400 pounds, giant porkers that toddled around on spindly legs. These pigs were not bred for their meat, however, but for their fat. If they are any bigger than 250 pounds, most American pigs stop putting on muscle and exclusively turned all of their consumed calories into fat. This extra fat was not desirable for eating or cooking, because it had very little flavor, but it didn\u2019t matter, because farmers were raising these hogs almost exclusively for the byproduct industry. By the 1850s the price of lard for soap, candles and especially lard oil was so high that many breeders and farmers focused on fat production over meat production. Some would not even bother to have their hogs butchered for pork, but would just sell them whole to fat-renderers, who would boil the whole hog down and sell it to various Cincinnati byproduct companies.'),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('p',null,'The market in Cincinnati was not just a conduit for Miami Valley agricultural products, but it also shaped the farms, landscape and animals of the region. The two were tied together, city and country, through pigs. But the connections did not stop there, because the pigs also brought the Miami Valley into the larger global economy during the 19th century, including its most important product, cotton. '),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('h3',null,'Feeding the Plantation'),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('p',null,'After the pigs were slaughtered, Cincinnati workers packed pork into a myriad array of products and foodstuffs. Almost all of it was destined for markets outside of the region, and so it had to be preserved through salting and smoking. The most valuable were the premium hams and bacons, which commanded a high price and often won prizes at international competitions. Most of the rest of the pork was packed into barrels of various cuts and grades. The two most desirable were \u201Cmess\u201D and \u201Cclear\u201D pork, which were whole sides of pork smoked and packed in salt, often sold to the U.S. Navy and other oceangoing vessels that needed preserved meat for long trips. The bottom grade, ironically named \u201Cprime,\u201D was pig heads, necks and other much less desirable cuts, salted but then also often packed in liquid brine.'),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('p',null,'Since pigs could only be slaughtered during the winter, packed pork would spend months in storage before being shipped down river in the early spring. For most of it, the first stop was New Orleans, America\u2019s great Southern emporium and the gateway to the Mississippi River Valley. From there, it would be shipped back up the coast to Virginia, New York and spots up and down the Eastern Seaboard. A significant portion also went to the islands in the Caribbean like Cuba and Jamaica, and some eventually to England, where prized hams and bacon fetched a high price. But every year, especially before the American Civil War, a good bit of that Cincinnati pork made its way back up the river, to the plantations of the Mississippi Valley. '),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('p',null,'During the first half of the 19th century, cotton spread from the Carolinas across the Deep South, eventually into east Texas. But the heart of cotton production was the incredibly fertile plantations of the Lower Mississippi River valley, which had some of the best soil in the world. These plantations fed the textile mills of New England and especially Great Britain. By the 1850s, up to eighty percent of cotton in British mills came from the United States. Laboring in those fields to produce that cotton were enslaved African Americans, and one of their primary food rations was pork, often from the Miami River Valley.'),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('p',null,'Most plantations owners gave each enslaved person about five pounds of pork a week as part of their rations, usually the saltiest and fattiest cuts. Often times this pork was from hogs raised on the plantation, or from nearby farms. But especially in parts of Mississippi and Louisiana, planters would be so focused on cotton production, and had such large plantations, that they needed to purchase rations on the open market on a regular basis. This is where Cincinnati and Miami Valley pork came in. Tens of thousands of the \u201Cprime\u201D barrels that were shipped south each year ended up in plantation storehouse, where the pork would be parsimoniously distributed as part of the meager rations of enslaved persons on cotton plantations. '),__WEBPACK_IMPORTED_MODULE_0_react___default.a.createElement('p',null,'Miami Valley pigs then, from farms like the Austin-Magie Farm, were an important part of not just the local and regional economy, but also the global one. They provided the primary ingredient for Cincinnati\u2019s great pig-processing machine, being turned into hundreds of different edible and non-edible products. Through that, they were shipped around the world, but helped feed the backbreaking labor on America\u2019s cotton plantations, which themselves were on the cutting edge of global capitalism, and fueling the industrial revolution. '));};var styles={blockQuote:{width:'75%',margin:'auto'},image:{width:'100%',height:'auto',margin:'15px 0px'},figure:{margin:'50px 0px'}};/* harmony default export */ __webpack_exports__["a"] = (__WEBPACK_IMPORTED_MODULE_1_react_jss___default()(styles)(PorkopolisWithClasses));

