import React from 'react';
import injectSheet from 'react-jss';
import EssayPage from '../../../Components/EssayPage';
import WeBelieveStatements from './images/webelievestatements.jpg';

const WeBelieveWithClasses = ({
  classes
}) => (
  <EssayPage 
    articleTitle='We Believe . . .'
  >
    <p>At the Institute for Food Design Charrette held on January 29-30, 2016, participants were asked to complete the statement, “We believe in an Institute for Food . . .”</p>
    <img
      src={WeBelieveStatements} 
      alt="We believe statements"
      className={classes.image}
    />
      <p>Here are the compiled results . . .</p>
      <p className={classes.weBelieveStatement}>We believe in an Institute for Food so that we can be responsible citizens of the earth.</p>
      <p className={classes.weBelieveStatement}>We believe the Institute for Food will make a difference and change the lives of all involved; it shows students, faculty, and community members how to work with and learn from all those who have varying interests, talents, skill sets, and backgrounds; it can redefine how the world views sustainability; and it creates lasting friendships and relationships, a knowledge base and skill set, which all involved can take with them into their future endeavors. </p>
      <p className={classes.weBelieveStatement}>We believe in an Institute for Food that educates both Miami students and the surrounding Oxford community about the meaning of health, sustainability, and long-term collaboration.</p>
      <p className={classes.weBelieveStatement}>We believe in an Institute for Food that embodies and emphasizes educational values and sustainable practices in order to make a difference one tomato at a time.</p>
      <p className={classes.weBelieveStatement}>We believe in an Institute for Food that engages people with a variety of interests in practices that promote community health now and into the future.</p>
      <p className={classes.weBelieveStatement}>We believe in an Institute for Food that creates and changes a regional economy based on cooperation, democratic participation, justice and good health for all—an institute that brings young and old folks from all parts of the community together in mutual support.</p>
      <p className={classes.weBelieveStatement}>We believe we can uplIFt students, faculty and the community by growing delicious, healthy food and initiating sustainable practices and embodying healthy lifestyles.</p>
      <p className={classes.weBelieveStatement}>We believe the Institute for Food at Miami University will be able to reach beyond the existing boundaries connecting communities, promote health, and ideally help feed those who are not able to feed themselves.</p>
      <p className={classes.weBelieveStatement}>We believe in an Institute for Food that will create community awareness about the importance of caring for our land in a way that can also benefit us by providing healthy food.</p>
      <p className={classes.weBelieveStatement}>We believe in an institute for Food that will create affordable food for students and for all.</p>
      <p className={classes.weBelieveStatement}>We believe in an Institute for Food that creates opportunities, which allows students to learn new skills; an Institute for Food that engages with the larger community as well as the environment; and provides a greater understanding of the region’s rich agricultural heritage.</p>
      <p className={classes.weBelieveStatement}>We believe in an Institute for Food that will establish a community, which recognizes the world’s current environmental and economic issues, and avidly works to improve future horizons by creating sustainable and community supported agriculture.</p>
      <p className={classes.weBelieveStatement}>We believe in an Institute for Food that connects people to their food, physically, psychologically, and spiritually.  It must highlight the inseparable interconnectedness of humanity and the natural environment and motivate changes in the lives of people who interact with it.  </p>
      <p className={classes.weBelieveStatement}>We believe in an Institute for Food that transcends the capitalist model of over production and is able to provide healthy, fresh, sustainable food to our people while minimizing its effects on our natural environment.</p>
      <p className={classes.weBelieveStatement}>We believe in an Institute for Food that creates a strong presence for students engaged on the farm AND on campus; an Institute for Food that extends beyond Miami University, influences and involves the community locally, regionally, and beyond; and an Institute for Food that encourages all involved to carry sustainable food knowledge beyond the local community and integrates practices into everyday life everywhere.</p>
      <p className={classes.weBelieveStatement}>We believe in an Institute for Food that supports the school and community with fresh, healthy, affordable food.</p>
      <p className={classes.weBelieveStatement}>We believe in an Institute for Food that takes advantage of the natural resources and land around our campus, that unites students from many different majors and backgrounds, and that provides affordable produce for students as well as opportunities for employment for students and community members alike.</p>
      <p className={classes.weBelieveStatement}>We believe in an Institute for Food that works with the community, the university, and the region to educate and immerse people in a sustainable farm environment that can improve people’s lives.</p>
      <p className={classes.weBelieveStatement}>We believe in an Institute for Food that encourages both the local and student communities to buy and eat local, sustainable food not only now but also after graduation and throughout their lives.</p>
      <p className={classes.weBelieveStatement}>We believe in an Institute for Food that brings together students and community members that would not normally interact; an Institute for Food that can provide jobs in a place where the community’s opportunities are lacking; an Institute for Food that can bring together students, faculty, and the community to work, eat, and grow together. What IF we can achieve this?</p>
      <p className={classes.weBelieveStatement}>We believe in an Institute for Food that engages the community and provides affordable fresh produce for our students and members of the community.</p>
      <p className={classes.weBelieveStatement}>We believe in an Institute for Food that will embody sustainability at every step in its process; an Institute for Food that will change attitudes at this school; and an Institute for Food that inspires others to always live sustainably.</p>
      <p className={classes.weBelieveStatement}>IF we act… we believe in an Institute for Food that can prepare students, faculty, and the community to transform the way in which we value and interact with the natural environment.</p>
      <p className={classes.weBelieveStatement}>We believe in an Institute for Food that can promote environmental literacy at Miami, in Oxford, and in the larger Miami Valley region.</p>
      <p className={classes.weBelieveStatement}>We believe Miami’s Institute for Food will be a small step in realizing global, social, and environmental food justice.</p>
      <p className={classes.weBelieveStatement}>We believe in an Institute for Food that promotes healthy living, collaborative learning, and a growing environment that supports every member of the Miami University and Oxford community.</p>
      <p className={classes.weBelieveStatement}>We believe in an Institute for Food that can connect us to our community so that we can gain the feeling of security in our home away from home.</p>
      <p className={classes.weBelieveStatement}>We believe in an Institute for Food that takes advantage of the university land we already have to bring together the communities of Miami University, Oxford, and the surrounding area.</p>
      <p className={classes.weBelieveStatement}>We believe in an Institute for Food that values biodiversity.</p>
      <p className={classes.weBelieveStatement}>We believe in an Institute for Food that can affect our global community.</p>
      <p className={classes.weBelieveStatement}>We believe in an Institute for Food that teaches the community the start to finish process of food growth.</p>
      <p className={classes.weBelieveStatement}>We believe in an Institute for Food that can help lower the price of campus meal plans for students and at the same time make our food healthier.</p>
      <p className={classes.weBelieveStatement}>We believe in an Institute for Food where a community can come together through the production of organic foods.</p>
      <p className={classes.weBelieveStatement}>We believe in an Institute for Food that inspires people about the importance of healthiness, sustainability and the environment.</p>
      <p className={classes.weBelieveStatement}>We believe in an Institute for Food that really brings people the healthiest food.</p>
      <p className={classes.weBelieveStatement}>We believe in an Institute for Food that connects diverse individuals of the Miami and Oxford communities in order to provide healthy and sustainable practices for our new combined and unified community.</p>
      <p className={classes.weBelieveStatement}>We believe in an Institute for Food that bridges the gap between the Miami “elite” and the impoverished Oxford community members.</p>
      <p className={classes.weBelieveStatement}>We believe that an Institute for Food can create community interconnectedness to promote value from start to finish of the process.</p>
      <p className={classes.weBelieveStatement}>We believe in an Institute for Food that provides students a path to an education and a life of adding value to the earth instead of taking value away and destroying the planet on which we depend for life.</p>
      <p className={classes.weBelieveStatement}>We believe in an Institute for Food that instills the values of care and engagement with the earth and each other.</p>
      <p className={classes.weBelieveStatement}>We believe in an Institute for Food that provides healthier alternatives for the students and faculty on campus as well as provide local businesses with fresher, healthier ingredients.</p>
      <p className={classes.weBelieveStatement}>We believe in an Institute for Food that brings together and improves relationships between students and the community, promotes and improves health brings together different disciplines at Miami University, and promotes and works towards a sustainable future.</p>
      <p className={classes.weBelieveStatement}>We believe in an Institute for Food that teaches lasting lessons about food that will improve our lifestyles and our planet in the long run.</p>
      <p className={classes.weBelieveStatement}>We believe in an Institute for Food that is a modern, self-sufficient place that brings people together.</p>
      <p className={classes.weBelieveStatement}>We believe in an Institute for Food that creates a realistic hands-on approach, which reveals where food comes from for all members of the Oxford community.</p>
      <p className={classes.weBelieveStatement}>We believe in an Institute for Food that promotes engagement, where people can come together on the production of food while showcasing our mutual need for food and the health benefits of good food, which will close the gap between students.</p>
      <p className={classes.weBelieveStatement}>We believe in an Institute for Food that educates and informs the greater Oxford community about the benefits of healthy, responsibly grown food.</p>
      <p className={classes.weBelieveStatement}>We believe in an Institute for Food that provides the university and our community with guidance towards a healthier lifestyle and opportunities to learn and understand how each of us can move toward being more sustainable.</p>
      <p className={classes.weBelieveStatement}>We believe community is not anonymous.</p>
      <p className={classes.weBelieveStatement}>We believe in an Institute for Food that provides participation along with collaboration to enhance and introduce new ideas for a sustainable future.</p>
      <p className={classes.weBelieveStatement}>We believe in an Institute for Food that promotes passionate education, sustainable agriculture, and a future for people on our planet</p>
      <p className={classes.weBelieveStatement}>We believe in an Institute for Food that promotes the value of knowledge regarding nutrition and the amount of time and work that goes into growing food as well as supporting community engagement and involvement.</p>
      <p className={classes.weBelieveStatement}>We believe in an Institute for Food that embodies the traditional idea of farming but also the intellectual opportunities of Miami University.</p>
      <p className={classes.weBelieveStatement}>We believe in an Institute for Food that educates the community about the importance of a healthy, sustainable future and the positive impact we can create by working together.</p>
      <p className={classes.weBelieveStatement}>We believe in an Institute for Food that serves as a meeting place and center for Miami students and the Oxford community; a place that increases the health and awareness of the university and the community.</p>
      <p className={classes.weBelieveStatement}>We believe in an Institute for Food that creates and builds healthy soils.</p>
      <p className={classes.weBelieveStatement}>We believe in an Institute for Food that creates healthy, delicious food for the community and the university as whole and that allows people to connect with the environment.</p>
      <p className={classes.weBelieveStatement}>We believe in an Institute for Food that engages the local community, increases student participation and enhances the knowledge of food systems and the environment.</p>
      <p className={classes.weBelieveStatement}>We believe in an Institute for Food that has a petting zoo with goats!</p>
      <p className={classes.weBelieveStatement}>We believe in an Institute for Food that creates a sustainable program for promoting community involvement, environmental education, and healthy living through a process that can continue into the future and continue to grow.</p>
      <p className={classes.weBelieveStatement}>We believe in an Institute for Food that promotes interdisciplinary programs and education to continue to learn from one another for years to come.</p>
      <p className={classes.weBelieveStatement}>We believe in an Institute for Food that begins a discussion on sustainable agriculture within our community and beyond to create a future-friendly lifestyle for our kids and grandkids.</p>
      <p className={classes.weBelieveStatement}>We believe in an Institute for Food that offers affordable organic products to community members as well as engaging elementary and high school aged students in Oxford to stress the future importance of these issues.</p>
      <p className={classes.weBelieveStatement}>We believe in an Institute for Food that will advocate an agenda of promoting access to fresh, affordable, and locally grown food for its lower-income community members.</p>
      <p className={classes.weBelieveStatement}>We believe in an Institute for Food that engages the community in educating themselves and creating a better understanding of sustainable food production—an institute that is an opportunity, not a business.</p>
      <p className={classes.weBelieveStatement}>We believe in an Institute for Food that creates a positive educational outlook about the importance of healthy food and care for the environment on the part of college students.</p>
  </ EssayPage>
);

const styles = {
  image: {
    width: '100%',
    height: 'auto',
  },
  weBelieveStatement: {
    fontStyle: 'italic',
  }
}

export default injectSheet(styles)(WeBelieveWithClasses);


