import React from 'react';
import injectSheet from 'react-jss';
import EssayPage from '../../../Components/EssayPage';
import LC1 from './images/LC1.jpg';
import LC2 from './images/LC2.jpg';
import LC3 from './images/LC3.jpg';
import LC4 from './images/LC4.jpg';
import LC5 from './images/LC5.jpg';
import LC6 from './images/LC6.jpg';
import LC7 from './images/LC7.jpg';
import LC8 from './images/LC8.jpg';
import LC9 from './images/LC9.jpg';
import LC10 from './images/LC10.jpg';
import LC11 from './images/LC11.jpg';
import LC12 from './images/LC12.jpg';
import LC13 from './images/LC13.jpg';
import LC14 from './images/LC14.jpg';
import LC15 from './images/LC15.jpg';
import LC16 from './images/LC16.jpg';
import LC17 from './images/LC17.jpg';
import LC18 from './images/LC18.jpg';
import LC19 from './images/LC19.jpg';
import LC20 from './images/LC20.jpg';
import LC21 from './images/LC21.jpg';
import LC22 from './images/LC22.jpg';
